.listGroup{
  list-style: none;
  padding: 0;
  margin: auto;
}

.todoList {
  margin: 30px auto;
  max-width: 600px;
}

@media (max-width: 768px) {
  .listGroup {
    max-width: 400px 
  }
}

@media (max-width: 480px) {
  .listGroup {
    max-width: 260px 
  }
}

@media (max-width: 320px) {
  .listGroup {
    max-width: 180px 
  }
}